.buttons {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-right: 20px;
}

@media(max-width: 767px) {
    .buttons {
        grid-template-columns: 1fr;
    }
}

.buttons__button {

    border: none;
    color: teal;
    background-color: white;
    transition: 1s;
}

@media(max-width: 767px) {
    .buttons__button {
        margin: 10px;

    }
}

.buttons__button:hover {
    color: hsl(180, 100%, 45%);
}

.buttons__button:disabled {
    color: rgb(199, 199, 199);
}