.section {
    background-color: white;
    margin: 10px;
    box-shadow: 0px 0px 19px -2px rgb(183, 183, 183);
  }
  
  .section__header {
    margin: 0;
    padding-bottom: 10px;
    padding: 20px;
    /* border-bottom: 2px solid rgb(231, 230, 230); */
  }
  
  @media (max-width: 767px) {
    .section__header--left {
      justify-self: start;
    }
  }
  
  .section__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid rgb(231, 230, 230);
  }
  
  @media (max-width: 767px) {
    .section__container {
      display: grid;
      grid-template-columns: 1fr;
      justify-items: center;
    }
  }